.nav-container {
  display: flex;
}
.nav-header {
  width: 50%;
}
.nav-header .logo {
  padding: 15px;
  margin-top: 40px;
  margin-left: 25px;
}

.bars {
  display: none;
}
.nav-desk {
  align-self: center;
}

.nav-desk nav ul {
  list-style: none;

  margin-left: auto;
}
.nav-desk nav ul li {
  display: inline;
}
.nav-desk nav ul li a {
  color: #000;
  font-size: 1.5rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-decoration: none;
  padding: 10px 15px;
  border: 1px solid transparent;
  margin: 4px 10px;

  transition: 0.3s;
}
.nav-desk nav ul li a:hover {
  border-bottom: 3px solid #000;
}

.nav-mobile {
  display: none;
}

/* for mobile devices */

@media screen and (max-width: 768px) {
  body,
  html {
    overflow-x: hidden;
  }
  .nav-container {
    justify-content: center;
  }
  .bars {
    display: block;

    align-self: center;
  }
  .top {
    align-self: start;
    position: absolute;
    right: 0;
    z-index: 4;
  }
  .nav-header .logo {
    margin-right: auto;
    margin-top: 1.3rem;
  }
  .nav-header {
    display: flex;
    padding: 10px;

    width: 90%;
    justify-self: center;
    align-self: center;
  }
  .nav-desk nav ul {
    display: none;
  }

  .nav-mobile {
    position: absolute;
    display: block;
    height: 100vh;
    background-color: rgb(0, 0, 0);
    color: #ffffff;
    visibility: hidden;
    right: 0;
    width: 0px;
    top: 0;
    transition: 0.3s;
  }
  .show {
    visibility: visible;
    width: 33%;
  }
  .nav-mobile ul {
    padding: 2rem;

    list-style: none;
  }
  .nav-mobile ul li {
    padding: 10px 15px;

    margin: 2rem;
  }
  .nav-mobile ul li a {
    color: #fff;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: 1.8rem;
    text-decoration: none;
    transition: 0.3s;
  }
  .nav-mobile ul li a:hover {
    cursor: pointer;
    color: rgb(51, 48, 48);
  }
}
